import React, { useContext, Fragment } from "react";
import AppContext from "app/appContext";
import { PDFDownloadLink } from "@react-pdf/renderer";
import ReportData from "./ReportData";
import { useHistory } from "react-router-dom";
const PdfReport = ({ classroomId }) => {
  const { classroomSlots, classrooms, subjects, teachers } =
    useContext(AppContext);
  const history = useHistory();
  const classroomSlot = classroomSlots.filter(
    (slot) => slot.classroomId === classroomId
  );
  const handleClick = () => {
    setTimeout(() => {
      history.push("/classrooms/classrooms-list");
    }, 5000);
  };
  return (
    <Fragment>
      <div className="mt-5"></div>
      {classroomSlot && (
        <ReportData
          classroomSlot={classroomSlot}
          classrooms={classrooms}
          teachers={teachers}
          subjects={subjects}
          classroomId={classroomId}
        />
      )}
      <PDFDownloadLink
        document={
          classroomSlot && (
            <ReportData
              classroomSlot={classroomSlot}
              classrooms={classrooms}
              teachers={teachers}
              subjects={subjects}
              classroomId={classroomId}
            />
          )
        }
        fileName="Test"
        className="mb-10"
      >
        {({ loading }) =>
          loading ? (
            <button className="btn btn-primary mt-5">
              Loading document...
            </button>
          ) : (
            <button className="btn btn-primary mt-5" onClick={handleClick}>
              Download
            </button>
          )
        }
      </PDFDownloadLink>
    </Fragment>
  );
};

export default PdfReport;
